/* Import modern font */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap');

:root {
  /* Colors */
  --color-background: #0a0a0a;
  --color-surface: #141414;
  --color-text: #ffffff;
  --color-text-secondary: rgba(255, 255, 255, 0.6);
  --color-text-disabled: rgba(255, 255, 255, 0.2);
  --color-border: rgba(255, 255, 255, 0.1);
  --color-hover: rgba(255, 255, 255, 0.1);
  --color-surface-hover: rgba(255, 255, 255, 0.05);
  --color-surface-disabled: rgba(255, 255, 255, 0.02);
  --color-accent-success: #4ade80;
  --color-accent-danger: #ff4466;
  --color-button-disabled: rgba(255, 255, 255, 0.03);
  
  /* Shadows */
  --shadow-sm: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  
  /* Border radius */
  --radius-sm: 3px;
  --radius-md: 6px;
  --radius-lg: 8px;
  
  /* Spacing */
  --space-xs: 4px;
  --space-sm: 8px;
  --space-md: 12px;
  --space-lg: 16px;
  --space-xl: 20px;
  --space-2xl: 24px;
}

/* Resetting html/body heights for the flex container */
html {
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, system-ui, sans-serif;
  font-weight: 400;
  letter-spacing: -0.011em;

  /* Dark theme background and text color */
  background: var(--color-background);
  color: var(--color-text);

  /* Enable flex layout */
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
}

/* Advertisement header */
.ad-header {
  background: var(--color-surface);
  border-bottom: 1px solid var(--color-border);
  text-align: center;
  padding: var(--space-sm);
  flex-shrink: 0;
}

/* Main header */
.main-header {
  background: var(--color-surface);
  color: var(--color-text);
  text-align: center;
  padding: var(--space-2xl);
  border-bottom: 1px solid var(--color-border);
  flex-shrink: 0;
}

.main-header h1 {
  margin: 0;
  font-size: 1.5rem;
  font-weight: 600;
  letter-spacing: -0.02em;
}

/* The main area grows to fill leftover space */
main {
  padding: var(--space-2xl);
  flex: 1 0 auto;
  border: 1px solid var(--color-border);
  display: flex;
  flex-direction: column;
  gap: var(--space-2xl);
  background: var(--color-background);
}

/* Chart container */
#race {
  border: 1px solid var(--color-border);
  min-height: 300px;
  background-color: var(--color-surface);
  width: 100%;
  border-radius: var(--radius-lg);
}

/* Footer at the bottom */
footer {
  background: var(--color-surface);
  text-align: center;
  padding: var(--space-lg);
  border-top: 1px solid var(--color-border);
  flex-shrink: 0;
  font-size: 0.9rem;
  color: var(--color-text-secondary);
}

/* Advertisement below chart */
.ad-below-chart {
  background: var(--color-surface);
  padding: var(--space-lg);
  text-align: center;
  border-top: 1px solid var(--color-border);
  flex-shrink: 0;
}

.ad-below-chart p {
  margin: 0;
}

/* Basic responsiveness */
@media (max-width: 768px) {
  body {
    font-size: 14px;
  }

  main {
    padding: var(--space-lg);
    gap: var(--space-lg);
  }

  .main-header h1 {
    font-size: 1.2rem;
  }

  .coin-selector-container,
  .available-coins-container {
    padding: var(--space-lg);
  }

  .search-container input {
    width: 150px;
  }
  
  .available-coins-header {
    flex-direction: column;
    align-items: stretch;
    gap: var(--space-md);
  }

  .coin-selector,
  .available-coins {
    max-height: 150px;
  }
}

/* Container styles */
.coin-selector-container,
.available-coins-container {
  margin: 0;
  padding: var(--space-xl);
  background: var(--color-surface);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-lg);
  width: 100%;
  box-sizing: border-box;
  box-shadow: var(--shadow-sm);
}

.coin-selector-header,
.available-coins-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: var(--space-lg);
}

.coin-selector-header h3,
.available-coins-header h3,
.coin-selector-container h3,
.available-coins-container h3 {
  margin: 0;
  font-size: 1.1rem;
  font-weight: 600;
  color: var(--color-text);
  letter-spacing: -0.01em;
}

.refresh-coins {
  display: inline-flex;
  align-items: center;
  gap: var(--space-xs);
  background: var(--color-surface-hover);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  color: var(--color-text-secondary);
  padding: var(--space-xs) var(--space-sm);
  font-size: 0.9rem;
  font-family: inherit;
  cursor: pointer;
  transition: all 0.2s ease;
}

.refresh-coins:hover:not(.disabled) {
  background: var(--color-hover);
  color: var(--color-text);
  border-color: var(--color-text-secondary);
}

.refresh-coins:active:not(.disabled) {
  transform: scale(0.98);
}

.refresh-coins .refresh-icon {
  font-size: 1.1em;
  display: inline-block;
  transition: transform 0.3s ease;
}

.refresh-coins:hover:not(.disabled) .refresh-icon {
  transform: rotate(180deg);
}

.refresh-coins.disabled {
  background: var(--color-button-disabled);
  color: var(--color-text-disabled);
  border-color: transparent;
  cursor: default;
  opacity: 0.5;
  pointer-events: none;
}

.coin-selector,
.available-coins {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-md);
  max-height: 200px;
  overflow-y: auto;
  padding: var(--space-xs);
  width: 100%;
  box-sizing: border-box;
  scrollbar-width: thin;
  scrollbar-color: var(--color-hover) transparent;
}

/* Webkit scrollbar styling */
.coin-selector::-webkit-scrollbar,
.available-coins::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.coin-selector::-webkit-scrollbar-track,
.available-coins::-webkit-scrollbar-track {
  background: transparent;
}

.coin-selector::-webkit-scrollbar-thumb,
.available-coins::-webkit-scrollbar-thumb {
  background: var(--color-hover);
  border-radius: var(--radius-sm);
}

.coin-selector::-webkit-scrollbar-thumb:hover,
.available-coins::-webkit-scrollbar-thumb:hover {
  background: var(--color-text-secondary);
}

/* For Firefox */
* {
  scrollbar-width: thin;
  scrollbar-color: #444 #1e1e1e;
}

.coin-item,
.available-coin-item {
  display: flex;
  align-items: center;
  background: var(--color-surface-hover);
  padding: var(--space-sm) var(--space-md);
  border-radius: var(--radius-md);
  transition: all 0.2s ease;
  cursor: pointer;
  backdrop-filter: blur(8px);
}

.coin-item:hover,
.available-coin-item:hover {
  background: var(--color-hover);
  transform: translateY(-1px);
}

.coin-item img,
.available-coin-item img {
  width: 24px;
  height: 24px;
  margin-right: var(--space-sm);
  border-radius: 50%;
}

.remove-coin,
.add-coin {
  background: none;
  border: none;
  color: var(--color-text-secondary);
  font-size: 20px;
  cursor: pointer;
  padding: 0 0 0 var(--space-sm);
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
}

.remove-coin:hover {
  color: var(--color-accent-danger);
}

.remove-coin:focus,
.add-coin:focus {
  outline: none;
}

.add-coin:hover {
  color: var(--color-accent-success);
}

.add-coin:disabled {
  color: var(--color-text-disabled);
  cursor: not-allowed;
}

.available-coin-item.disabled {
  opacity: 0.5;
  background: var(--color-surface-disabled);
  cursor: not-allowed;
  transform: none;
}

.search-container {
  position: relative;
  display: flex;
  align-items: center;
}

.search-container input {
  background: var(--color-surface-hover);
  border: 1px solid var(--color-border);
  border-radius: var(--radius-md);
  color: var(--color-text);
  padding: var(--space-sm) 32px var(--space-sm) var(--space-md);
  font-size: 0.95rem;
  width: 200px;
  transition: all 0.2s ease;
  font-family: inherit;
}

.search-container input:focus {
  outline: none;
  border-color: var(--color-text-secondary);
  background: var(--color-hover);
  box-shadow: 0 0 0 2px var(--color-surface-hover);
}

.search-container input::placeholder {
  color: var(--color-text-secondary);
}

.search-icon {
  position: absolute;
  right: var(--space-sm);
  color: var(--color-text-secondary);
  font-size: 14px;
  pointer-events: none;
}
